.icon{
    fill: #173d33;
    width: 24px;
    height: 24px;
    -webkit-transition-property: fill;
    transition-property: fill;
    -webkit-transition-duration: .3s;
            transition-duration: .3s;
    -webkit-transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
            transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
            margin-right: 8px;
}
.button_icon{
    fill: #173d33;
     width: 16px;
    height: 16px;
    -webkit-transition-property: fill;
    transition-property: fill;
    -webkit-transition-duration: .3s;
            transition-duration: .3s;
    -webkit-transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
            transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.button{  
        position: relative;
        z-index: 2;
        display: inline-flex;
        justify-content: center;
        align-items: center;      
        height: 32px;
        width: 32px;
        border-radius: 50%;
        border: none;
        background-color: #97D28B;
        cursor: pointer;
        -webkit-transition-property: background-color;
        transition-property:background-color;
        -webkit-transition-duration: .3s;
                transition-duration: .3s;
        -webkit-transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
                transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.button:hover{
    background-color: #173d33;
}
.button:hover .button_icon{
    fill: #97D28B;
}
.contacts{
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
}
.contacts__link{
    display: flex; 
    justify-content: center;
    align-items: center;
    font-family: 'FiraSans';
    font-size: 16px;
    font-style: normal;
    letter-spacing: -0.04em;
    color: #173D33;
}

.social__link:hover .icon{
    fill: #97D28B;
}

.contacts_socials{
    display: flex; 
    justify-content: center;
    align-items: center;
}


.flex_container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;   
  }
.flex_container::before{
    position: absolute;
    content: '';
    top: -12px;
    left: 0;
    right: 0;
    height: 1px;
    background-color: #97D28B;   
  }

  .flex_button{
    display: flex;
    justify-content: flex-end;
  }
 
  .flex_logo,
  .flex_button{
    width: 50%;
  }
  .flex_socials{
    order: 1;
  }

@media only screen and (min-width: 360px) and (max-width: 480px) {
    .section {
      padding:  18px 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .flex_socials{
        margin: 24px auto;
    }
    .contacts__link:not(:last-child){
        margin-bottom: 16px;
    }
   
}
@media only screen and (min-width: 480px) and (max-width: 767px) {
    .section {
      padding:  18px 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .contacts{
        width: 440px;       
    }
    .flex_socials{
        margin: 24px auto;
    }
    .contacts__link:not(:last-child){
        margin-bottom: 16px;
    }
    
   
}

  @media screen and (min-width: 768px) {
    .section {
     padding:  25px 0;
    }
    .contacts{
        /* width: 768px; */
        flex-direction: row; 
        justify-content: space-between;
        margin-top: 16px;      
    }
    .contacts_socials{
        justify-content: flex-start;
    }
  
      .flex_socials {
        width: 50%;
        left: 50%;
        margin-left: -50%;
      }
      .flex_container::before{    
        top: -20px;
      }
 
}
    
  @media screen and (min-width: 1440px) {
    .section {
     padding:  30px 0;
    }
    .contacts{
        width: 1240px;
    }
   

}